/**
 *  ,      -.
 *     ()
 */

//  z-index
@import './z-index';

//  -
@import './baseColors';

@import '~@hse-design/tokens/lib/scss/radius.scss';
@import '~@hse-design/tokens/lib/scss/sizing.scss';
@import '~@hse-design/tokens/lib/scss/spacing.scss';
@import '~@hse-design/tokens/lib/scss/transitions.scss';
@import '~@hse-design/tokens/lib/scss/shadow.scss';
@import '~@hse-design/tokens/lib/scss/sidebarWidth.scss';

@import 'mixins/index.scss';

//    
@import './baseText';
@import './em-sizing';

//  
$spacing-40: $spacing-medium-x;

//   
$input-size-small: $sizing-32;
$input-size-medium: $sizing-40;
$input-size-large: $sizing-48;

//       
$icon-size-small: $sizing-16;
$icon-size-medium: $sizing-24;
$icon-size-large: $sizing-32;

$iconSizeTypes: 'small', 'medium', 'large';
$iconSizes: $icon-size-small, $icon-size-medium, $icon-size-large;
$pi: 3.1415926;

//  
$header-height: $sizing-56; // tablet-small  
$header-height-small: $sizing-48; // mobile-large  

$sidebar-icon-size: $sidebar-width-tablet-large-dynamic;

$scrollbar-width: 17px;

/* stylelint-disable-next-line */
:export {
  headerHeight: $header-height;
  headerHeightSmall: $header-height-small;
  sidebarIconSize: $sidebar-icon-size;
}
