@import 'mixins/respond';
///        
/// <breakpoint>-down-hide       
/// <breakpoint>-up-hide       
/// <breakpoint>-only-hide      
/// <breakpoint>-down-show       
/// <breakpoint>-up-show       
/// <breakpoint>-only-show      

@each $name, $value in $breakpoints-map {
  $name: str-replace($name, $breakpointPrefix, '');

  .#{$name}-down-hide {
    @include respond-down($name) {
      @include hide-content;
    }
  }

  .#{$name}-up-hide {
    @include respond-up($name) {
      @include hide-content;
    }
  }

  .#{$name}-only-hide {
    @include respond($name) {
      @include hide-content;
    }
  }

  .#{$name}-down-show {
    $nextBreakpoint: get-next-breakpoint($name);

    @if $nextBreakpoint {
      @include respond-up($nextBreakpoint) {
        @include hide-content;
      }
    }
  }

  .#{$name}-up-show {
    $prevBreakpoint: get-prev-breakpoint($name);

    @if $prevBreakpoint {
      @include respond-down($prevBreakpoint) {
        @include hide-content;
      }
    }
  }

  .#{$name}-only-show {
    $prevBreakpoint: get-prev-breakpoint($name);
    $nextBreakpoint: get-next-breakpoint($name);

    @if ($prevBreakpoint) {
      @include respond-down($prevBreakpoint) {
        @include hide-content;
      }
    }

    @if ($nextBreakpoint) {
      @include respond-up($nextBreakpoint) {
        @include hide-content;
      }
    }
  }
}
